<template>
    <Button class="px-2" text severity="secondary" @click="showDialog = true">
        <i :class="`ti ${currentBatteryIcon}`"></i>
    </Button>
    
    <Dialog class="kitchen-dlg" v-model:visible="showDialog" modal header="バッテリー" :style="{ width: '25rem' }">
        <i :class="`ti ${currentBatteryIcon}`"></i>
        <p>状態: {{ currentBatteryState }}</p>
        <p>使用率: {{ currentBattery }}</p>
    </Dialog>
</template>

<script>
import Button from 'primevue/button';
import Dialog from 'primevue/dialog';
export default {
    components: {
        Button, Dialog
    },
    data() {
        return {
            currentBatteryIcon: '',
            currentBatteryState: '',
            currentBattery: '',
            showDialog: false
        }
    },
    mounted() {
        this.updateBattery();
    },
    methods: {
        updateBattery() {
            const bat = navigator.getBattery();
            if(!bat) {
                this.currentBatteryIcon.value = 'ti-battery-off';
                this.currentBatteryState.value = '不明';
                this.currentBattery.value = 'この環境では取得できません';
                return;
            }
            bat.then(battery => {
                const updateLevelInfo = () => {
                    const batteryLevel = battery.level * 100;
                    this.currentBattery = `${batteryLevel}%`;
                    if(!battery.charging) {
                        if (batteryLevel >= 90) {
                            this.currentBatteryIcon = 'ti-battery-4';
                        } else if (batteryLevel >= 75) {
                            this.currentBatteryIcon = 'ti-battery-3';
                        } else if (batteryLevel >= 50) {
                            this.currentBatteryIcon = 'ti-battery-2';
                        } else if (batteryLevel >= 25) {
                            this.currentBatteryIcon = 'ti-battery-1';
                        } else {
                            this.currentBatteryIcon = 'ti-battery-charging-2';
                            this.currentBatteryState = '直ちに充電してください';
                        }
                    }
                };
                battery.addEventListener('levelChange', updateLevelInfo);
                const updateChargeInfo = () => {
                    this.currentBatteryIcon = `${battery.charging ? 'ti-battery-charging' : updateLevelInfo()}`;
                    this.currentBatteryState = `${battery.charging ? '充電中' : '放電中'}`;
                }
                battery.addEventListener('chargingchange', updateChargeInfo);

                const updateAllBatteryInfo = () => {
                    updateChargeInfo();
                    updateLevelInfo();
                }
                updateAllBatteryInfo();
            });
        }
    }
}
</script>

<style>
.kitchen-dlg .p-dialog-header span {
    font-size: 1.5rem;
}
</style>
