<template>
    <Button class="px-2" text severity="secondary" style="padding: 0.25rem 0.5rem">
        {{ currentTime }}
    </Button>
</template>

<script>
import Button from 'primevue/button';

export default {
    data() {
        return {
            currentTime: '',
            weekdays: ['日', '月', '火', '水', '木', '金', '土']
        }
    },
    components: {
        Button
    },
    methods: {
        updateTime() {
            const now = new Date()
            const hours = now.getHours().toString().padStart(2, '0')
            const minutes = now.getMinutes().toString().padStart(2, '0')
            this.currentTime = `${hours}:${minutes} (${this.weekdays[now.getDay()]})`;
        }
    },
    mounted() {
        this.updateTime();
    },

    beforeMount() {
        setInterval(this.updateTime, 1000);
    }
}
</script>

<style>
.p-button-text {
    height: 100%;
}
</style>
